body {
  margin: 0;
  padding: 0;
  background: #f2f4f5;
  color: #1e1e1f;
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
}

#root {
  margin: 0 24px;
  position: relative;
  min-height: 100vh;
}


.TodoCounter {
  font-size: 24px;
  text-align: center;
  margin: 0;
  padding: 48px;
  font-family: "Montserrat";
}

.TodoItem {
  background-color: #fafafa;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  border-radius: 10px;
}

.TodoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
}
.TodoItem-p--complete {
  text-decoration: line-through;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
  /* background-color: #CCC; */
}

.Icon-check {
  position: absolute;
  left: 12px;
}
.Icon-check--active {
  color: #4caf50;
}

.Icon-delete {
  position: absolute;
  top: -24px;
  right: 0;
}
.Icon-delete:hover {
  color: red;
}

ul {
    margin: 0;
    padding: 0 0 56px 0;
    list-style: none;
}
.TodoSearch {
  background: #f9fbfc;
  border-radius: 10px;
  /* border: 2px solid #202329; */
  border: none;
  margin: 0 24px;
  height: 64px;
  width: calc(100% - 62px);
  font-size: 24px;
  text-align: center;
  font-family: "Montserrat";
  font-weight: 400;
  color: #1e1e1f;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch::-webkit-input-placeholder {
  color: #a5a5a5;
  font-family: "Montserrat";
  font-weight: 400;
}

.TodoSearch::placeholder {
  color: #a5a5a5;
  font-family: "Montserrat";
  font-weight: 400;
}

.TodoSearch:focus {
  box-shadow: 0px 5px 50px rgba(140, 211, 252, 0.25);  
  outline-color: #f9fbfc;
}

.CreateTodoButton {
  background-color: #61dafa;
  box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  font-size: 50px;
  position: fixed;
  bottom: 24px;
  right: 24px;
  font-weight: bold;
  color: #fafafa;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 64px;
  width: 64px;

  -webkit-transform: rotate(0);

          transform: rotate(0);
  transition: 0.3s ease;
  z-index: 1;
}

.CreateTodoButton:hover {
  -webkit-transform: rotate(224deg);
          transform: rotate(224deg);
}

.ModalBackground {
  background: rgba(32, 35, 41, 0.8);
  position: fixed;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white<;
}

form {
  width: 90%;
  max-width: 300px;
  background-color: #fff;
  padding: 33px 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 10px;
}

label {
  text-align: center;
  font-weight: bold;
  font-size: 20px;
  color: #1e1e1f;
  margin-bottom: 26px;
}

textarea {
  background-color: #f9fbfc;
  border: 2px solid #202329;
  border-radius: 2px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  color: #1e1e1f;
  font-size: 20px;
  text-align: center;
  padding: 12px;
  height: 96px;
  width: calc(100% - 25px);
  resize: none;
  border-radius: 10px;
  border: none;
}

textarea::-webkit-input-placeholder {
  color: #a5a5a5;
  font-family: "Montserrat";
  font-weight: 400;
}

textarea::placeholder {
  color: #a5a5a5;
  font-family: "Montserrat";
  font-weight: 400;
}

textarea:focus {
  outline-color: #f9fbfc;
  box-shadow: 0px 5px 50px rgba(157, 211, 247, 0.986)
}

.TodoForm-buttonContainer {
  margin-top: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.TodoForm-button {
  cursor: pointer;
  display: inline-block;
  font-size: 20px;
  color: #202329;
  font-weight: 400;
  width: 120px;
  height: 48px;
  border-radius: 8px;
  border: none;
  font-family: "Montserrat";
}

.TodoForm-button--add {
  background: #61dafa;
  box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  color: #f9fbfc;
}

.TodoForm-button--cancel {
  background: transparent;
}

.LoadingTodo-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  height: 75px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  border-radius: 10px;
}

.LoadingTodo-text {
  margin: 24px 0px 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
}

.LoadingTodo-deleteIcon,
.LoadingTodo-completeIcon {
  border-radius: 50px;
  height: 48px;
  width: 48px;
  font-size: 24px;
}

.LoadingTodo-deleteIcon {
  position: absolute;
  top: -20px;
  right: 6px;
}

.LoadingTodo-completeIcon {
  position: absolute;
  left: 12px;
  bottom: 12px;
}

.LoadingTodo-container,
.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon{
    background: linear-gradient(90deg, rgba(250,250,250,1), rgb(206, 234, 250));
    background-size: 400% 400%;
    -webkit-animation: loadingAnimations 3s ease-in-out infinite;
            animation: loadingAnimations 3s ease-in-out infinite;
}

@-webkit-keyframes loadingAnimations {
    0%{
        background-position: 0% 50%;
    }
    50%{
        background-position: 100% 50%;
    }    
    100%{
        background-position: 0% 50%;
    }        
}

@keyframes loadingAnimations {
    0%{
        background-position: 0% 50%;
    }
    50%{
        background-position: 100% 50%;
    }    
    100%{
        background-position: 0% 50%;
    }        
}
.EmptyTodo-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70vh;
}

.EmptyTodo-text {
  font-size: xx-large;
  font-weight: 600;
}

